<template>
  <div class="flex flex-column tab-content">
    <div v-if="form.dictName === ''">
      <div class="my-code">
        点击字典列表查看详情
      </div>
    </div>
    <yl-table
      v-else
      v-loading="loading.fetchData"
      v-bind="tableProps"
    >
      <template slot="body(action)" slot-scope="{ row }">
        <el-button type="text" @click="handleEdit(row)">
          编辑
        </el-button>
        <el-button type="text" @click="handleDel(row)">
          删除
        </el-button>
      </template>
    </yl-table>
    <addOrEditDetailDialog ref="addOrEditDetailDialog" @success="handleSearch" />
  </div>
</template>

<script>
import searchPageMixin from '@thales/search-page';
import { searchDictField, delDictField } from '@/api/table';
import loadingMixin from '@thales/loading';
import { detailTableFields } from './field';
import addOrEditDetailDialog from './addOrEditDetailDialog.vue';

export default {
  name: 'DictDetailManage',
  components: {
    addOrEditDetailDialog,
  },
  mixins: [searchPageMixin, loadingMixin],
  data() {
    return {
      form: {
        dictName: '',
      },
      dictId: null,
      tableIdentify: null,
      // tableData: [{ name: '132', desc: 'xxxxx' }, { name: 'xxxx' }],
      tableData: [],
      tableHeight: '60vh',
      loading: {
        fetchData: false,
      },
    };
  },
  computed: {
    tableProps() {
      return {
        autofit: false,
        tableHeaderKey: 'dicDetailManage',
        height: this.tableHeight,
        fields: detailTableFields,
        border: true,
        total: this.total,
        data: this.tableData,
        pageSizeKey: 'size',
        currentKey: 'page',
        pageParam: {
          size: 10,
          page: 1,
        },
        ...this.mixinTableProps,
      };
    },
  },
  mounted() {
    setTimeout(() => {
      this.tableHeight = 'calc(100vh - 290px)';
    }, 50);
  },
  methods: {
    handleEnable(row, type) {
      const rowData = row;
      rowData.status = type;
      this.tableData.splice(this.tableData.indexOf(row), 1, rowData);
      this.onQuery();
    },
    async handleDel(row) {
      await this.$confirm('确定删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      });
      // this.tableData.splice(index, 1);
      await delDictField({ id: row.id });
      this.$message.success('操作成功');
      this.onQuery();
    },
    handleSearch() {
      // this.tableData.push(row);
      this.onQuery();
    },
    handleAdd() {
      this.$refs.addOrEditDetailDialog.show(this.dictId, this.tableIdentify);
    },
    handleEdit(row) {
      this.$refs.addOrEditDetailDialog.showEdit(row);
    },

    async fetchData() {
      // const params = { ...form };
      // console.log(params);
      // Object.keys(params).forEach((key) => {
      //   if (isEmpty(params[key])) {
      //     delete params[key];
      //   }
      // });

      const params = {
        tableIdentify: this.tableIdentify,
        id: this.dictId,
      };
      // const { page, size, ...rest } = params;

      const res = await searchDictField(
        params,
      );
      this.tableData = res;
      this.total = this.tableData.length;
    },
    handleKeyUpQuery({ keyCode }) {
      if (keyCode === 13) {
        this.onQuery();
      }
    },
  },
};
</script>

