  padding: 20px;
  flex: 1;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  background-color: #fff;
  margin: 0 10px;
}
.my-code {
  position: relative;
  padding: 15px;
  line-height: 20px;
  border-left: 5px solid #ddd;
  color: #333;
  font-family: Courier New, serif;
  font-size: 12px;
}
