<template>
  <el-dialog
    v-dialogDrag
    :title="isEdit?'编辑':'新增'"
    :visible.sync="visible"
    top="15vh"
    width="36%"
    append-to-body
    :close-on-click-modal="false"
    @close="hide"
  >
    <div class="dialog_content adjust-content">
      <el-form
        ref="form"
        :model="form"
        label-width="140px"
        :rules="rules"
        label-position="right"
      >
        <el-form-item label="类型" prop="fieldType">
          <!-- <el-input
            v-model.trim="form.fieldType"
          /> -->
          <el-select v-model="form.fieldType" placeholder="请选择类型">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="标签值" prop="fieldIdentify">
          <el-input
            v-model.trim="form.fieldIdentify"
          />
          <!-- <b-ace-editor
            v-model="form.fieldIdentify"
            lang="json"
            height="200"
            theme="chrome"
            :readonly="false"
            :font-size="12"
          /> -->
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">
        取 消
      </el-button>
      <el-button
        type="primary"
        :loading="loading.confirm"
        @click="confirm"
      >
        确 定
      </el-button>
    </div>
  </el-dialog>
</template>

<script>
import loadingMixin from '@thales/loading';
import { createDictField, updateDictField } from '@/api/table';
import { STATUS } from './field';

export default {
  mixins: [loadingMixin],
  data() {
    return {
      dictId: null,
      tableIdentify: null,
      STATUS,
      isEdit: false,
      loading: {
        confirm: false,
      },
      visible: false,
      form: {
        fieldType: '',
        fieldIdentify: '',
      },
      rules: {
        fieldType: [{ required: true, message: '请选择', trigger: 'blur' }],
        fieldIdentify: [{ required: true, message: '请输入', trigger: 'blur' }],
      },
      id: null,
      options: [
        {
          label: '单体',
          value: 'ONE',
        },
        {
          label: '数组',
          value: 'LIST',
        },
        {
          label: '对象',
          value: 'OBJECT',
        },
        {
          label: '数组对象',
          value: 'LIST_OBJECT',
        },

      ],
    };
  },
  mounted() {},
  created() {},
  methods: {
    fetchData() {},
    async confirm() {
      await this.$refs.form.validate();
      const { fieldType, fieldIdentify } = this.form;
      let params = {
        fieldType,
        fieldIdentify,
        tableIdentify: this.tableIdentify,
        tableId: this.dictId,
        id: this.id ? this.id : undefined,
      };
      if (!this.isEdit) {
        params = {
          param: [params],
        };
      }

      const API = this.isEdit ? updateDictField : createDictField;
      await API(params);
      this.$emit('success');
      this.hide();
    },
    cancel() {
      this.hide();
    },
    showEdit(data) {
      console.log(data);
      this.isEdit = true;
      this.id = data.id;

      this.form = {
        ...data,
        // fieldIdentify: JSON.stringify(data.fieldIdentify, null, 2),
      };
      this.visible = true;
    },
    show(dictId, tableIdentify) {
      this.isEdit = false;
      this.dictId = dictId;
      this.tableIdentify = tableIdentify;
      this.id = null;
      this.form = {
        fieldType: '',
        fieldIdentify: '',
      };
      this.visible = true;
    },
    hide() {
      this.$refs.form.resetFields();
      this.visible = false;
    },
  },
};
</script>
<style lang="scss" scoped>
.adjust-content ::v-deep {
  .el-form-item {
    margin-bottom: 20px;
  }
}
</style>
