<template>
  <div class="flex flex-column tab-content">
    <!-- 字典列表 -->
    <el-row :gutter="10">
      <el-col
        :xs="24"
        :sm="24"
        :md="10"
        :lg="11"
        :xl="11"
        style="margin-bottom: 10px"
      >
        <el-card class="box-card">
          <yl-form-collapse collapse-position="right" collapse-height="42px">
            <template slot="form">
              <yl-form
                v-model="form"
                size="mini"
                v-bind="formProps"
              />
            </template>
            <template slot="collapse">
              <el-row class="flex flex-row justify-end">
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleSearch"
                >
                  查询
                </el-button>
                <el-button
                  size="mini"
                  @click="handleReset"
                >
                  重置
                </el-button>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleAdd"
                >
                  新增
                </el-button>
              </el-row>
            </template>
          </yl-form-collapse>
          <yl-table
            v-loading="loading.fetchData"
            v-bind="tableProps"
            highlight-current-row
            @current-change="handleCurrentChange"
          >
            <template slot="body(action)" slot-scope="{ row }">
              <el-button
                type="text"
                @click="handleEdit(row)"
              >
                编辑
              </el-button>
              <el-button
                type="text"
                @click="handleDel(row)"
              >
                删除
              </el-button>
            </template>
          </yl-table>
        </el-card>
      </el-col>
      <!-- 字典详情列表 -->
      <el-col
        :xs="24"
        :sm="24"
        :md="14"
        :lg="13"
        :xl="13"
      >
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>字典详情</span>
            <el-button
              v-if="$refs.dictDetail && $refs.dictDetail.form.dictName"
              class="filter-item"
              size="small"
              style="float: right;padding: 4px 10px"
              type="primary"
              icon="el-icon-plus"
              @click="$refs.dictDetail && $refs.dictDetail.handleAdd()"
            >
              新增
            </el-button>
          </div>
          <dictDetail ref="dictDetail" />
        </el-card>
      </el-col>
    </el-row>
    <addOrEditDialog ref="addOrEditDialog" @success="handleSearch" />
  </div>
</template>

<script>
import searchPageMixin from '@thales/search-page';
import { searchDict, delDict } from '@/api/table';
import loadingMixin from '@thales/loading';
import { formFields, tableFields } from './field';
import addOrEditDialog from './addOrEditDialog.vue';
import dictDetail from './dictDetail.vue';

export default {
  name: 'DictManage',
  components: {
    addOrEditDialog,
    dictDetail,
  },
  mixins: [searchPageMixin, loadingMixin],
  data() {
    return {
      form: {},
      // tableData: [{ name: '132', desc: 'xxxxx' }, { name: 'xxxx' }],
      tableData: [],
      tableHeight: '60vh',
      shippingOrderDetails: {
        visible: false,
        item: {},
      },
      loading: {
        fetchData: false,
        handleDel: false,
      },
    };
  },
  computed: {
    formProps() {
      return {
        fields: formFields,
        fieldCol: {
          xl: 6,
          lg: 6,
          md: 8,
        },
        wrapperWidth: '80%',
        total: 10,
        nextFields: [
          {
            key: 'name',
            nativeOn: {
              keyup: this.handleKeyUpQuery,
            },
            on: {
              clear: () => {
                this.onQuery();
              },
            },
          },
        ],
      };
    },
    tableProps() {
      return {
        autofit: false,
        tableHeaderKey: 'dicManage',
        height: this.tableHeight,
        fields: tableFields,
        border: true,
        total: this.total,
        data: this.tableData,
        pageSizeKey: 'size',
        currentKey: 'page',
        pageParam: {
          size: 10,
          page: 1,
        },
        ...this.mixinTableProps,
      };
    },
  },
  mounted() {
    setTimeout(() => {
      this.tableHeight = 'calc(100vh - 290px)';
    }, 50);
  },
  methods: {
    handleClearDetail() {
      if (this.$refs.dictDetail) {
        this.$refs.dictDetail.form.dictName = '';
      }
      return true;
    },
    handleSearch() {
      // this.tableData.push(row);
      this.handleClearDetail();
      this.onQuery();
    },
    handleReset() {
      // this.tableData.push(row);
      this.handleClearDetail();
      this.onReset();
    },

    // 选中字典后，设置字典详情数据
    handleCurrentChange(val) {
      // console.log('val', val);
      if (val) {
        this.$refs.dictDetail.form.dictName = val.name;
        this.$refs.dictDetail.dictId = val.id;
        this.$refs.dictDetail.tableIdentify = val.tableIdentify;
        this.$refs.dictDetail.fetchData();
      }
    },
    handleEnable(row, type) {
      const rowData = row;
      rowData.status = type;
      this.tableData.splice(this.tableData.indexOf(row), 1, rowData);
      this.onQuery();
    },
    async handleDel(row) {
      await this.$confirm('确定删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      });
      // this.tableData.splice(index, 1);
      await delDict({ id: row.id });
      this.$message.success('操作成功');
      this.handleSearch();
    },
    handleAdd() {
      this.$refs.addOrEditDialog.show();
    },
    handleEdit(row) {
      this.$refs.addOrEditDialog.showEdit(row);
    },

    async fetchData(form) {
      const params = { ...form };
      // Object.keys(params).forEach((key) => {
      //   if (isEmpty(params[key])) {
      //     delete params[key];
      //   }
      // });
      const { page, size, ...rest } = params;
      const res = await searchDict(
        { ...rest },
        {
          page: params.page,
          size: params.size,
        },
      );
      this.tableData = res;
      this.total = this.tableData.length;
    },
    handleKeyUpQuery({ keyCode }) {
      if (keyCode === 13) {
        this.onQuery();
      }
    },

  },
};
</script>

