import { flowCloudHttp } from '@/utils/http';

// ---------------- 节点相关数据接口--------------------

// 查询节点列表
export function selectNode(data) {
  return flowCloudHttp.request({
    url: '/flow_cloud/select/node',
    method: 'post',
    data,
  });
}

// 新增节点
export function createNode(data) {
  return flowCloudHttp.request({
    url: '/flow_cloud/create/node',
    method: 'post',
    data,
  });
}

