<template>
  <el-dialog
    v-dialogDrag
    :title="isEdit?'编辑':'新增'"
    :visible.sync="visible"
    top="15vh"
    width="36%"
    append-to-body
    :close-on-click-modal="false"
    @close="hide"
  >
    <div class="dialog_content adjust-content">
      <el-form
        ref="form"
        :model="form"
        label-width="140px"
        :rules="rules"
        label-position="right"
      >
        <el-form-item label="字典名称" prop="name">
          <el-input
            v-model.trim="form.name"
          />
        </el-form-item>
        <el-form-item label="字典标识" prop="tableIdentify">
          <el-input
            v-model.trim="form.tableIdentify"
          />
        </el-form-item>
        <el-form-item label="字典描述" prop="desc">
          <el-input
            v-model.trim="form.desc"
            type="textarea"
          />
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">
        取 消
      </el-button>
      <el-button
        type="primary"
        :loading="loading.confirm"
        @click="confirm"
      >
        确 定
      </el-button>
    </div>
  </el-dialog>
</template>

<script>
import loadingMixin from '@thales/loading';
import { createDict, updateDict } from '@/api/table';
import { STATUS } from './field';

export default {
  mixins: [loadingMixin],
  data() {
    return {
      STATUS,
      isEdit: false,
      loading: {
        confirm: false,
      },
      visible: false,
      form: {
      },
      rules: {
        name: [{ required: true, message: '请输入名称', trigger: 'blur' }],
        tableIdentify: [{ required: true, message: '请输入字典标识', trigger: 'blur' }],
      },
      id: null,
    };
  },
  mounted() {},
  created() {},
  methods: {
    fetchData() {},
    async confirm() {
      await this.$refs.form.validate();
      const params = {
        ...this.form,
        id: this.id ? this.id : undefined,
      };

      const API = this.isEdit ? updateDict : createDict;
      await API(params);
      this.$emit('success');
      this.hide();
    },
    cancel() {
      this.hide();
    },
    showEdit(data) {
      this.isEdit = true;
      this.id = data.id;
      this.form = {
        ...data,
      };
      this.visible = true;
    },
    show() {
      this.isEdit = false;
      this.id = null;
      this.form = {
      };
      this.visible = true;
    },
    hide() {
      this.$refs.form.resetFields();
      this.visible = false;
    },
  },
};
</script>
<style lang="scss" scoped>
.adjust-content ::v-deep {

  .el-form-item {
    margin-bottom: 20px;
  }
}
</style>
